#experience{
    padding-top: 2rem;
}
.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
}

.experience__container>div {
    background: var(--color-bg2);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container>div:hover {
    background: transparent;
    border-color: var(--color-cta2);
}

.experience__container>div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-cta);
    font-size: 2rem;
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.experience__details {
    display: flex;
    gap: 1rem;
}
.exp_de_icons {
    margin-top: 6px;
    color: var(--color-cta);
    font-size: 1.2rem;
}


/*  ##################  Media Queries (MEDIUM DEVICES)  ######################   */

@media screen and (max-width:1024px) {
    .experience__container{
        grid-template-columns: 1fr;
    }
    .experience__container>div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto; 
        align-items: center;  
    }
    .experience__content{
        padding: 1rem;
    }
    

}



/*  ##################  Media Queries (SMALL DEVICES)  ######################   */

@media screen and (max-width:600px) {
    .experience__container{
        gap: 2rem;
    }
    .experience__container>div{
        width: 100%;
        padding: 2rem 1rem;
    }
}